import React from 'react';
import Modal from 'react-responsive-modal';
import { BwmResError } from '../shared/form/BwmResError';

export function BookingModal(props) {
  const { open,
    closeModal,
    booking,
    confirmModal,
    errors,
    rentalPrice,
    acceptPayment,
    disabled } = props;

  return (
    <Modal open={open} onClose={closeModal} little classNames={{ modal: 'booking-modal' }}>
      <h4 className="modal-title title"> </h4>
      <p className="dates">{booking.startAt} / {booking.endAt}</p>
      <div className="modal-body">
        <em>{booking.days}</em>  /
        <em>{rentalPrice}$</em>  
        <p>: <em>{booking.guests}</em></p>
        <p>: <em>{booking.totalPrice}$ </em></p>

        {acceptPayment && acceptPayment()}

        <p>    ?</p>
      </div>
      <BwmResError errors={errors} />
      <div className="modal-footer">
        <button disabled={disabled} onClick={confirmModal} type="button" className="btn btn-primary"></button>
        <button type="button" onClick={closeModal} className="btn btn-primary"></button>
      </div>
    </Modal>
  );
}
